import { useState, useEffect, useCallback } from 'react'
import { inject, observer } from 'mobx-react'
import { formatAddress, eqInNumber } from '../../utils/utils'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import Button from '../../components/Button/Button'
import apiProxy from '../../model/ApiProxy'
import timeLogo from './img/time.svg'
import tokenLogo from './img/pToken.svg'
import './airdrop.less'
import useContract from '../../hooks/useContract';
import { DERI_TOKEN_ADDRESS, USDC_TOKEN_ADDRESS, EVENT_TRANS_BEGIN, EVENT_TRANS_END } from '../../utils/Constants'
import Emitter from '../../utils/Emitter'
const { BigNumber, ethers, utils } = require("ethers");

function AriDrop({ lang, wallet = {}, type = 'deri' }) {
  const [balance, setBalance] = useState(0);
  const [disabled, setDisabled] = useState(true);
  const alert = useAlert();
  const [info, setInfo] = useState({})
  const [isWhite, setIsWhite] = useState(false)
  const [isPToken, setIsPToken] = useState(true)
  const [isExist, setIsExist] = useState(false)
  const contract = useContract('airDrop')
  const balanceContract = useContract("ERC20USDC")
  const transactionTitle = {
    text: "Claim Reward",
    icon: "USDC"
  }
  const transactionContent = {
    success: "Claim Reward",
    error: "Transaction Failed"
  }

  const getBalance = async () => {
    if (wallet.isConnected() && contract && eqInNumber(wallet.detail.chainId, 42161)) {
      const balances = await contract.getBalances(USDC_TOKEN_ADDRESS, [wallet.detail.account]);
      if (balances[0] && BigNumber.from(balances[0]._hex).toNumber()) {
        console.log(BigNumber.from(balances[0]._hex).toString())
        setIsExist(true)
      } else {
        setIsExist(false)
      }
    }
  }

  const getWalletBalance = async () => {
    if (+wallet.detail.chainId === 42161 && balanceContract) {
      let balance = await balanceContract.balanceOf(wallet.detail.account)
      let decimals = await balanceContract.decimals()
      balance = BigNumber.from(balance._hex).toString() / (10 ** decimals)
      setBalance(balance)
    }
  }
  const click = useCallback(async () => {
    if (wallet.isConnected() && contract && wallet.detail.chainId === 42161) {
      let res;
      try {
        res = await contract.claim(USDC_TOKEN_ADDRESS)
      } catch (error) {
        console.log(error)
        if (error.error && error.error.data && error.error.data.message) {
          let message = error.error.data.message
          alert.error(message)
        }
        return false;
      }
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle, content: transactionContent, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            getBalance()
            // getWalletBalance()
            setDisabled(true)
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: true, hash: hash, error: "error" } })
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: false.success, hash: hash, error: "error" } })
          }

        }
      }
    }
  }, [contract, wallet.detail.chainId])

  useEffect(() => {
    if (wallet.isConnected()) {
      getBalance()
      // getWalletBalance()
    }

  }, [wallet.chainId, wallet.account])

  useEffect(() => {
    if (isExist) {
      let timestamp = new Date()
      if (timestamp.getTime() >= 1682071200000) {
        setDisabled(true)
      } else {
        setDisabled(false)
      }
    } else {
      setDisabled(true)
    }
  }, [isExist])

  return (
    <div className='airdrop-box'>
      <div className='airdrop-info'>
        <div className='airdrop-info-box'>
          <div className='airdrop-title'>
            {/* {lang['title']} */}
            {/* $1000 Giveaway for FTT Holders */}
            Deri Protocol 2nd Year Anniversary Winners
          </div>
          <div className='wallet-address'>
            <span className='blue-logo'></span>
            {formatAddress(wallet.detail.account)}
          </div>
          {/* <div className='wallet-balance'>
            {lang['wallet-balance']} : <DeriNumberFormat value={balance} decimalScale={2} thousandSeparator={true} /> USDC
          </div> */}
          {/* {type === 'pToken' && <div className='airdrop-time position-text'>
            <div className='time-logo'>
              <img src={tokenLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  Get free mBTC^2 perpetual
                </div>
                <div>
                  powers position
                </div>
              </div>
            </div>
          </div>} */}
          {type === 'usdc' && <div className='airdrop-time position'>
            <div className='time-logo'>
              <img src={timeLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  {lang['start-time']}
                </div>
                <div>
                  {lang['expire-time']}
                </div>
              </div>
              <div>
                <div>
                  2023-04-17 08:00:00 UTC
                </div>
                <div>
                  2023-04-21 10:00:00 UTC
                </div>
              </div>
            </div>
          </div>}
          {type === 'deri' && <div className='airdrop-time'>
            <div className='time-logo'>
              <img src={timeLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  {lang['start-time']}
                </div>
                <div>
                  {lang['expire-time']}
                </div>
              </div>
              <div>
                <div>
                  2022-03-09 10:00:00 UTC
                </div>
                <div>
                  2022-03-11 12:00:00 UTC
                </div>
              </div>
            </div>
          </div>}

          <div className='claim'>
            <Button fontWeight='bold' disabled={disabled} label={lang['claim']} width='277' height='72' fontSize='32' onClick={click} />
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(AriDrop))